import React, { useState } from 'react';
import axios from 'axios';
import './setting.css';
import './Register.css';
import './ForgotPassword.css';
import { useNavigate } from 'react-router-dom';
import logo from '../../images/IMG_5602.png'; // import

const ForgotPassword = () => {
  const [email, setEmail] = useState(''); // 
  const [errorMessage, setErrorMessage] = useState(''); // 
  const [successMessage, setSuccessMessage] = useState(''); // 
  const navigate = useNavigate();

  const handleForgotPassword = async (e) => {
    e.preventDefault();
  
    // 
    setSuccessMessage('');
    setErrorMessage('');
  
    if (!email.includes('@')) {
      setErrorMessage('');
      return;
    }
  
    try {
      const response = await axios.post('https://alc-streamersland.com/forgot_password', {
        email: email,
      });
  
      if (response.status === 200) {
        setSuccessMessage('');
      }
    } catch (error) {
      setErrorMessage('');
    }
  };

  return (
    <div className="setting">
      <div className="body-1">
        <div className="RegisterFormTop">
        <img src={logo} className="image-styleSL" alt="" /> {/*  */}
          <div className="p-text3">
            <strong></strong>
          </div>
          <form onSubmit={handleForgotPassword}>
            <div className="formFont">
              <input
                type="email"
                id="email"
                name="email"
                size="30"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder=""
                required
                autoComplete="off"
              />
            </div>

            {errorMessage && (
              <div className="error-message" style={{ color: 'red' }}>
                {errorMessage}
              </div>
            )}
            
            {successMessage && (
              <div className="success-message" style={{ color: 'green' }}>
                {successMessage}
              </div>
            )}

            <div className="submit">
              <button type="submit"></button>
            </div>
            <div className="login-link-postion">
            <a href="/login" className="login-link"></a>
              </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default ForgotPassword;
