import React, { useState } from 'react';
import axios from 'axios';
import './Help.css';
import './Help-tablet.css';

const HelpForm = () => {
  const [inquiryType, setInquiryType] = useState('');  
  const [userName, setUserName] = useState('');  
  const [email, setEmail] = useState('');  
  const [content, setContent] = useState('');  
  const [isFocused, setIsFocused] = useState(false);  
  const [screenshot, setScreenshot] = useState(null);  

  // 
  const [message, setMessage] = useState(''); // 
  const [messageType, setMessageType] = useState(''); // 'success'  'error'

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file && file.size > 5 * 1024 * 1024) {
      setMessage('5MB');
      setMessageType('error');
      setScreenshot(null);
    } else {
      setScreenshot(file);
      setMessage(''); // 
    }
  };

  // 
  const handleSubmit = (e) => {
    e.preventDefault();

    const formData = new FormData();
    formData.append('inquiryType', inquiryType);
    formData.append('userName', userName);
    formData.append('email', email);
    formData.append('content', content);

    if (screenshot) {
      formData.append('screenshot', screenshot);
    }

    axios.post('http://localhost:3001/api/inquiries_image', formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
    .then(response => {
      setMessage('');
      setMessageType('success');

      // 
      setInquiryType('');
      setUserName('');
      setEmail('');
      setContent('');
      setScreenshot(null);
    })
    .catch(error => {
      console.error(':', error);
      setMessage('');
      setMessageType('error');
    });
  };

  return (
<form onSubmit={handleSubmit} encType="multipart/form-data" className="form-container">
  {/*  */}
  {message && (
    <div className={`message ${messageType}`}>
      {message}
    </div>
  )}

  {/*  */}
  <label className="label-text"><strong></strong></label>
  <div className="radio-option1">
    <input 
      type="radio" 
      id="bug" 
      name="inquiryType" 
      value="bug" 
      onChange={() => setInquiryType('bug')} 
    />
    <div>
      <label className="labelText"><strong></strong></label>
    </div>
    <div className="radio-text2">
      <div className="radio-option2">
        <input 
          type="radio" 
          id="other" 
          name="inquiryType" 
          value="other" 
          onChange={() => setInquiryType('other')} 
        />
      </div>
      <label htmlFor="other"><strong></strong></label>
    </div> 
  </div>
  
  {/*  */}
  <div className="form-group">
    <label className="labelText2" htmlFor="userName"><strong></strong>
    <span className="colorLabel"><strong>*</strong></span>
  </label>
  </div>       
  <input 
    type="text" 
    id="userName" 
    value={userName} 
    onChange={(e) => setUserName(e.target.value)} 
    placeholder="()"
    required 
    className="form-input"
  />

  {/*  */}
  <div className="form-group">
    <label className="labelText3" htmlFor="email"><strong></strong>
    <span className="colorLabel"><strong>*</strong></span></label>
  </div> 
  <input 
    type="email" 
    id="email" 
    value={email} 
    onChange={(e) => setEmail(e.target.value)} 
    placeholder="stremersland@stremarsland.com"
    required 
    className="form-input"
  />

  {/*  */}
  <div className="form-group">
    <label className="labelText" htmlFor="content"><strong></strong>
    <span className="colorLabel"><strong>*</strong></span></label>
  </div> 
  <textarea 
    id="content" 
    value={content} 
    onChange={(e) => setContent(e.target.value)} 
    onFocus={() => setIsFocused(true)}
    onBlur={() => setIsFocused(content !== '')}
    placeholder={
      inquiryType === 'bug' && !isFocused ? 
      "" : 
      inquiryType === 'other' && !isFocused ? 
      "" : 
      ""
    }
    required
    className="form-input-context"
  ></textarea>

  {/* */}
  <div className={`form-group file-upload ${inquiryType === 'bug' ? 'visible' : ''}`}>
    <label className="labelText" htmlFor="screenshot"><strong></strong></label>
    <label htmlFor="screenshot" className="custom-file-upload"></label>
    <input 
      type="file" 
      id="screenshot" 
      accept="image/*" 
      onChange={handleFileChange} 
      className="form-input-file" 
    />
  </div>

  <div className="button-container">
    <button type="submit" className="submit-hbutton"></button>
  </div>
</form>
  );
};

export default HelpForm;
