import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './editForm.css'; // editFormCSS
import Flatpickr from 'react-flatpickr';
import 'flatpickr/dist/themes/light.css'; // CSS

function NewsForm({ selectedNews, onClose, onUpdate }) {
  const [date, setDate] = useState(new Date());
  const [title, setTitle] = useState('');
  const [category, setCategory] = useState('');
  const [image, setImage] = useState(null);
  const [url, setUrl] = useState('');

  useEffect(() => {
    if (selectedNews) {
      setDate(selectedNews.date ? new Date(selectedNews.date) : new Date());
      setTitle(selectedNews.title || '');
      setCategory(selectedNews.category || '');
      setUrl(selectedNews.url || '');
      setImage(null);
    }
  }, [selectedNews]);

  const handleSubmit = async (e) => {
    e.preventDefault();

    const urlPattern = /^(https?:\/\/)?([a-zA-Z0-9-]+\.)+[a-zA-Z]{2,}(\/\S*)?$/;
    if (!urlPattern.test(url)) {
      alert('URL');
      return;
    }

    const formData = new FormData();
    formData.append('news[date]', date ? date.toISOString().split('T')[0] : '');
    formData.append('news[title]', title);
    formData.append('news[category]', category);
    formData.append('news[url]', url);
    if (image) formData.append('news[image]', image);

    try {
      if (selectedNews) {
        await axios.put(`http://localhost:3001/api/news/${selectedNews.id}`, formData, {
          headers: { 'Content-Type': 'multipart/form-data' }
        });
        alert('');
      } else {
        const response = await axios.post('http://localhost:3001/api/news', formData, {
          headers: { 'Content-Type': 'multipart/form-data' }
        });
        if (response.status >= 200 && response.status < 300) {
          alert('');
        }
      }
      
      onUpdate();
      handleResetForm();
      onClose();
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  };

  const handleResetForm = () => {
    setDate(new Date());
    setTitle('');
    setCategory('');
    setUrl('');
    setImage(null);
  };

  return (
    <form onSubmit={handleSubmit} encType="multipart/form-data" className="news-post-form">
      <div className="form-group-news">
        <label htmlFor="date"></label>
        <Flatpickr
          value={date}
          onChange={(newDate) => setDate(newDate)}
          options={{ dateFormat: 'Y-m-d' }}
          className="form-control"
        />
      </div>

      <div className="form-group-news">
        <label htmlFor="category"></label>
        <select
          id="category"
          className="form-control"
          value={category}
          onChange={(e) => setCategory(e.target.value)}
        >
          <option value=""></option>
          <option value=""></option>
        </select>
      </div>

      <div className="form-group-news">
        <label htmlFor="title"></label>
        <input
          type="text"
          id="title"
          className="form-control"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          placeholder=''
          required
        />
      </div> 

      <div className="form-group-news">
        <label htmlFor="url">URL</label>
        <input
          type="url"
          id="url"
          className="form-control"
          value={url}
          onChange={(e) => setUrl(e.target.value)}
          placeholder='https//www.news.com'
          required
        />
      </div>

      <div className="form-group-news">
        <label htmlFor="image"></label>
        <input
          type="file"
          id="image"
          className="form-control"
          onChange={(e) => setImage(e.target.files[0])}
          accept="image/*"
        />
      </div>

      <div className="form-buttons">
        <button type="submit" className="submit-button">
          {selectedNews ? '' : ''}
        </button>
        {selectedNews && (
          <button type="button" onClick={() => {
            handleResetForm();
            onClose();
          }} className="cancel-button">
            
          </button>
        )}
      </div>
    </form>
  );
}

export default NewsForm;
