import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './NewsList.css';
import noImage from '../../../images/20200505_noimage.jpg'; // 
import homeIcon from '../../../images/.png'; 

function NewsListPage() {
  const [news, setNews] = useState([]);

  // 
  const fetchNews = async () => {
    try {
      const response = await axios.get('http://localhost:3001/api/news');
      setNews(response.data);
    } catch (error) {
      console.error('Error fetching news:', error);
    }
  };

  useEffect(() => {
    fetchNews();
  }, []);

  const isTwoColumns = news.length >= 2;

  return (
    <div>
      {/* WordPress */}
      <div className="home-link">
        <a href="http://test.local/" rel="noopener noreferrer">
          <img src={homeIcon} alt="" className="home-icon" />
        </a>
      </div>

      {/*  */}
      <div className={`news-list-container ${isTwoColumns ? 'two-columns' : ''}`}>
        <div className="titleNews">
          <h2>News</h2>
        </div>
        <div className="wp-news-list">
          {news.length > 0 ? (
            news.map((item) => (
              <div className="news-item" key={item.id}>
                <div className="news-content">
                  {/*  */}
                  <img
                    src={item.image_url || noImage}
                    alt={item.title}
                    className="news-image"
                  />
                  {/*  */}
                  <div className="news-title">
                    <a href={item.url} target="_blank" rel="noopener noreferrer">
                      {item.title}
                    </a>
                  </div>
                  <div className="news-category2">{item.category}</div>
                  <div className="news-date">: {new Date(item.date).toLocaleDateString()}</div>
                </div>
              </div>
            ))
          ) : (
            <p></p>
          )}
        </div>
      </div>
    </div>
  );
}

export default NewsListPage;
